import React, { useState, useEffect } from "react";
import { Loader2, Save, Plus, Trash2, CheckCircle, Briefcase, GraduationCap } from "lucide-react";
import { useAuth } from "../../context/AuthContext";
import toast from "react-hot-toast";

const categoryNames: any = {
  career: "ඔබගේ රැකියාව",
  personal: "පුද්ගලික තොරතුරු",
  property: "දේපළ තොරතුරු",
  family: "පවුල් තොරතුරු",
  physical: "ශාරීරික ලක්ෂණ",
  travel: "විදේස් සංචාර",
  education: "අධ්‍යාපන තොරතුරු",
  interests: "රුචිකත්ව",
  health: "සෞඛ්‍ය තොරතුරු",
  sports: "ක්‍රීඩා",
  family_deceased: "මිය ගිය පවුල් සාමාජිකයින්",
};

interface SurveyComponentProps {
  onLoadStart?: () => void;
  onLoadComplete?: () => void;
}

// Grouped Multi Select Component
const GroupedMultiComboSelect = ({ options, placeholder, onChange, value }: any) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const filteredOptions = Object.entries(options).reduce((acc: any, [category, items]) => {
    const filtered = (items as string[]).filter((item) =>
      item.toLowerCase().includes(searchTerm.toLowerCase())
    );
    if (filtered.length > 0) {
      acc[category] = filtered;
    }
    return acc;
  }, {});

  const handleSelect = (option: string, category: string) => {
    const isSelected = value.some((v: any) => v.option === option);
    if (isSelected) {
      onChange(value.filter((v: any) => v.option !== option));
    } else {
      onChange([...value, { option, category }]);
    }
  };

  const handleRemove = (option: string) => {
    onChange(value.filter((v: any) => v.option !== option));
  };

  return (
    <div className="relative">
      <div className="border-2 border-gray-200 rounded-lg p-3 min-h-[50px] flex flex-wrap gap-2">
        {value.map((item: any, index: number) => (
          <span
            key={index}
            className="bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm flex items-center gap-2"
          >
            {item.option}
            <button
              onClick={() => handleRemove(item.option)}
              className="hover:text-blue-600"
            >
              ×
            </button>
          </span>
        ))}
        <input
          type="text"
          placeholder={value.length === 0 ? placeholder : ""}
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          onFocus={() => setIsOpen(true)}
          className="flex-1 outline-none min-w-[200px]"
        />
      </div>

      {isOpen && (
        <>
          <div
            className="fixed inset-0 z-10"
            onClick={() => setIsOpen(false)}
          />
          <div className="absolute z-20 w-full mt-2 bg-white border-2 border-gray-200 rounded-lg shadow-xl max-h-80 overflow-y-auto">
            {Object.entries(filteredOptions).map(([category, items]) => (
              <div key={category}>
                <div className="bg-gray-100 px-4 py-2 font-semibold text-gray-700 sticky top-0">
                  {category}
                </div>
                {(items as string[]).map((item) => {
                  const isSelected = value.some((v: any) => v.option === item);
                  return (
                    <button
                      key={item}
                      onClick={() => handleSelect(item, category)}
                      className={`w-full text-left px-4 py-2 hover:bg-blue-50 ${
                        isSelected ? "bg-blue-100 font-medium" : ""
                      }`}
                    >
                      {item}
                    </button>
                  );
                })}
              </div>
            ))}
            {Object.keys(filteredOptions).length === 0 && (
              <div className="px-4 py-8 text-center text-gray-500">
                No results found
              </div>
            )}
          </div>
        </>
      )}
    </div>
  );
};

export default function UserFeedbackSurvey({
  onLoadStart,
  onLoadComplete,
}: SurveyComponentProps) {
  const {
    user,
    GetSurveyQuestions,
    GetUserSurveyAnswers,
    GetDeceasedFamily,
    DeleteDeceasedMember,
    SaveSurveyAnswers,
    saveDeceasedFamily,
    getJobsOptions,
    getEducationOptions,
    updateProfile,
  } = useAuth();
  
  const [questions, setQuestions] = useState<any[]>([]);
  const [answers, setAnswers] = useState<any>({});
  const [deceasedMembers, setDeceasedMembers] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [saveSuccess, setSaveSuccess] = useState(false);

  // Jobs and Education states
  const [selectedJobs, setSelectedJobs] = useState<{ option: string; category: string }[]>([]);
  const [selectedEducation, setSelectedEducation] = useState<{ option: string; category: string }[]>([]);
  const [jobsOptions, setJobsOptions] = useState<{ [key: string]: string[] }>({});
  const [educationOptions, setEducationOptions] = useState<{ [key: string]: string[] }>({});

  useEffect(() => {
    const loadData = async () => {
      if (!user) return;

      onLoadStart?.();
      setLoading(true);

      try {
        const [questionsData, answersData, deceasedData, jobs, edu] = await Promise.all([
          GetSurveyQuestions(),
          GetUserSurveyAnswers(),
          GetDeceasedFamily(),
          getJobsOptions(),
          getEducationOptions(),
        ]);

        if (questionsData.success) {
          setQuestions(questionsData.data);
        }
        if (answersData.success) {
          setAnswers(answersData.data || {});
        }
        if (deceasedData.success) {
          setDeceasedMembers(deceasedData.data || []);
        }
        
        setJobsOptions(jobs);
        setEducationOptions(edu);

      } catch (error) {
        console.error("Error loading survey data:", error);
        toast.error("Failed to load survey data");
      } finally {
        setLoading(false);
        onLoadComplete?.();
      }
    };

    loadData();
  }, [user]);

  useEffect(() => {
    if (user && questions.length > 0) {
      const updatedAnswers = { ...answers };

      const jobsQuestion = questions.find(
        (q) => q.question_key === "preferred_jobs"
      );
      if (jobsQuestion && user.jobs && user.jobs.length > 0) {
        updatedAnswers["preferred_jobs"] = user.jobs;
      }

      const educationQuestion = questions.find(
        (q) => q.question_key === "education_qualifications"
      );
      if (educationQuestion && user.education && user.education.length > 0) {
        updatedAnswers["education_qualifications"] = user.education;
      }

      setAnswers(updatedAnswers);
    }
  }, [user, questions]);

  // Initialize jobs and education from user data
  useEffect(() => {
    const findCategory = (optionsObj: { [key: string]: string[] }, option: string): string => {
      for (const [cat, opts] of Object.entries(optionsObj)) {
        if (opts.includes(option)) {
          return cat;
        }
      }
      return "";
    };

    if (Object.keys(jobsOptions).length > 0 && user?.jobs) {
      setSelectedJobs(
        user.jobs.map((job: string) => ({
          option: job,
          category: findCategory(jobsOptions, job),
        }))
      );
    }

    if (Object.keys(educationOptions).length > 0 && user?.education) {
      setSelectedEducation(
        user.education.map((edu: string) => ({
          option: edu,
          category: findCategory(educationOptions, edu),
        }))
      );
    }
  }, [jobsOptions, educationOptions, user]);

  const shouldShowQuestion = (question: any) => {
    if (!question.parent_question_key) return true;

    const parentAnswer: any = answers[question.parent_question_key];
    if (!parentAnswer) return false;

    return parentAnswer === question.parent_answer_condition;
  };

  const handleAnswerChange = (questionKey: any, value: any) => {
    setAnswers((prev: any) => {
      const newAnswers = {
        ...prev,
        [questionKey]: value,
      };

      // Clear child answers if condition no longer met
      const children = questions.filter((q: any) => q.parent_question_key === questionKey);
      children.forEach((child: any) => {
        if (value !== child.parent_answer_condition) {
          delete newAnswers[child.question_key];
        }
      });

      return newAnswers;
    });

    // Special handling for deceased family
    if (questionKey === "has_deceased_family" && value === "no") {
      setDeceasedMembers([]);
    }
  };

  const handleMultiSelectChange = (questionKey: any, option: any) => {
    const currentAnswers = answers[questionKey] || [];
    const newAnswers = currentAnswers.includes(option)
      ? currentAnswers.filter((item: any) => item !== option)
      : [...currentAnswers, option];

    setAnswers((prev: any) => ({
      ...prev,
      [questionKey]: newAnswers,
    }));
  };

  const addDeceasedMember = () => {
    setDeceasedMembers((prev: any) => [
      ...prev,
      {
        id: Date.now(),
        relationship: "",
        date_of_birth: "",
        birth_time: "",
        birth_location: "",
        year_of_death: "",
        cause_of_death: "",
      },
    ]);
  };

  const removeDeceasedMember = async (id: any) => {
    if (typeof id === "number" && id < Date.now() - 1000000) {
      try {
        await DeleteDeceasedMember(id);
      } catch (error) {
        console.error("Error deleting member:", error);
        toast.error("Failed to delete member");
        return;
      }
    }

    setDeceasedMembers((prev: any) =>
      prev.filter((member: any) => member.id !== id)
    );
  };

  const updateDeceasedMember = (id: any, field: any, value: any) => {
    setDeceasedMembers((prev: any) =>
      prev.map((member: any) =>
        member.id === id ? { ...member, [field]: value } : member
      )
    );
  };

  const handleJobsChange = (selected: { option: string; category: string }[]) => {
    setSelectedJobs(selected);
  };

  const handleEducationChange = (selected: { option: string; category: string }[]) => {
    setSelectedEducation(selected);
  };

  const handleSave = async () => {
    setSaving(true);

    try {
      // Add jobs and education to answers
      const finalAnswers = {
        ...answers,
        preferred_jobs: selectedJobs.map(j => j.option),
        education_qualifications: selectedEducation.map(e => e.option),
      };

      const answersData: any = await SaveSurveyAnswers(finalAnswers);

      if (!answersData.success) {
        throw new Error("Failed to save answers");
      }

      if (answers.has_deceased_family === "yes" && deceasedMembers.length > 0) {
        const deceasedData = await saveDeceasedFamily(deceasedMembers);

        if (!deceasedData.success) {
          throw new Error("Failed to save deceased family members");
        }
      }

      // Update profile with new jobs and education
      const profileData: any = {
        jobs: selectedJobs.map((s) => s.option),
        education: selectedEducation.map((s) => s.option),
      };
      await updateProfile(profileData);

      toast.success("Survey saved successfully!");
      setSaveSuccess(true);

      setTimeout(() => setSaveSuccess(false), 3000);
    } catch (error: any) {
      console.error("Save error:", error);
      toast.error(error.message || "Failed to save survey");
    } finally {
      setSaving(false);
    }
  };

  const renderQuestionInput = (question: any) => {
    const value = answers[question.question_key] || "";

    switch (question.question_type) {
      case "yes_no":
        return (
          <div className="flex gap-4">
            <button
              onClick={() => handleAnswerChange(question.question_key, "yes")}
              className={`flex-1 py-3 px-6 rounded-lg font-medium transition-all ${
                value === "yes"
                  ? "bg-green-600 text-white shadow-lg"
                  : "bg-gray-100 text-gray-700 hover:bg-gray-200"
              }`}
            >
              ඔව්
            </button>
            <button
              onClick={() => handleAnswerChange(question.question_key, "no")}
              className={`flex-1 py-3 px-6 rounded-lg font-medium transition-all ${
                value === "no"
                  ? "bg-red-600 text-white shadow-lg"
                  : "bg-gray-100 text-gray-700 hover:bg-gray-200"
              }`}
            >
              නැත
            </button>
          </div>
        );

      case "select":
        return (
          <select
            value={value}
            onChange={(e) =>
              handleAnswerChange(question.question_key, e.target.value)
            }
            className="w-full p-3 border-2 border-gray-200 rounded-lg focus:border-purple-500 focus:ring-2 focus:ring-purple-200 transition-all"
          >
            <option value="">තෝරන්න...</option>
            {question.options?.map((option: any, idx: any) => (
              <option key={idx} value={option}>
                {option}
              </option>
            ))}
          </select>
        );

      case "multi_select":
        return (
          <div className="space-y-2">
            {question.options?.map((option: any, idx: any) => (
              <label
                key={idx}
                className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg hover:bg-gray-100 cursor-pointer transition-all"
              >
                <input
                  type="checkbox"
                  checked={(value || []).includes(option)}
                  onChange={() =>
                    handleMultiSelectChange(question.question_key, option)
                  }
                  className="w-5 h-5 text-purple-600 rounded focus:ring-2 focus:ring-purple-500"
                />
                <span className="font-medium">{option}</span>
              </label>
            ))}
          </div>
        );

      case "text":
        return (
          <input
            type="text"
            value={value}
            onChange={(e) =>
              handleAnswerChange(question.question_key, e.target.value)
            }
            className="w-full p-3 border-2 border-gray-200 rounded-lg focus:border-purple-500 focus:ring-2 focus:ring-purple-200 transition-all"
            placeholder="ඔබේ පිළිතුර..."
          />
        );

      case "number":
        return (
          <input
            type="number"
            value={value}
            onChange={(e) =>
              handleAnswerChange(question.question_key, e.target.value)
            }
            className="w-full p-3 border-2 border-gray-200 rounded-lg focus:border-purple-500 focus:ring-2 focus:ring-purple-200 transition-all"
            placeholder="සංඛ්‍යාව..."
          />
        );

      case "year":
        return (
          <input
            type="number"
            value={value}
            onChange={(e) =>
              handleAnswerChange(question.question_key, e.target.value)
            }
            className="w-full p-3 border-2 border-gray-200 rounded-lg focus:border-purple-500 focus:ring-2 focus:ring-purple-200 transition-all"
            placeholder="වර්ෂය..."
            min="1900"
            max={new Date().getFullYear()}
          />
        );

      case "date":
        return (
          <input
            type="date"
            value={value}
            onChange={(e) =>
              handleAnswerChange(question.question_key, e.target.value)
            }
            className="w-full p-3 border-2 border-gray-200 rounded-lg focus:border-purple-500 focus:ring-2 focus:ring-purple-200 transition-all"
          />
        );

      case "color":
        return (
          <input
            type="color"
            value={value || "#000000"}
            onChange={(e) =>
              handleAnswerChange(question.question_key, e.target.value)
            }
            className="w-full h-12 rounded-lg cursor-pointer"
          />
        );

      default:
        return null;
    }
  };

  const groupedQuestions = questions.reduce((acc: any, question: any) => {
    const category: any = question.category || "other";
    if (!acc[category]) acc[category] = [];
    acc[category].push(question);
    return acc;
  }, {});

  if (loading) {
    return (
      <div className="min-h-screen bg-linear-to-br from-purple-50 to-blue-50 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-16 h-16 animate-spin text-purple-600 mx-auto mb-4" />
          <p className="text-xl text-gray-700">ප්‍රශ්න පූරණය කරමින්...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-linear-to-br from-purple-50 via-blue-50 to-indigo-50 py-8 px-4">
      <div className="w-full mx-auto">
        <div className="bg-white rounded-3xl shadow-2xl overflow-hidden mb-8">
          <div className="bg-linear-to-r from-purple-600 to-indigo-600 p-8 text-white">
            <h1 className="text-3xl font-bold mb-2">
              පරිශීලක තොරතුරු සමීක්ෂණය
            </h1>
            <p className="text-purple-100">
              ඔබේ ජන්ම පත්‍රය වඩාත් නිවැරදිව විශ්ලේෂණය කිරීම සඳහා මෙම තොරතුරු
              අත්‍යව්‍යයි
            </p>
          </div>

          <div className="p-8 space-y-8">
            {/* Jobs Section */}
            <div className="space-y-6">
              <h2 className="text-2xl font-bold text-gray-800 border-b-2 border-purple-200 pb-2 flex items-center gap-3">
                <Briefcase className="w-7 h-7 text-purple-600" />
                ඔබගේ රැකියාව
              </h2>
              <div className="bg-linear-to-r from-gray-50 to-gray-100 p-6 rounded-xl border-l-4 border-purple-500">
                <label className="block mb-3">
                  <span className="text-lg font-semibold text-gray-800">
                    ඔබගේ රැකියාව තෝරන්න
                  </span>
                </label>
                <GroupedMultiComboSelect
                  options={jobsOptions}
                  placeholder="ඔබගේ රැකියාව සොයන්න..."
                  onChange={handleJobsChange}
                  value={selectedJobs}
                />
              </div>
            </div>

            {/* Education Section */}
            <div className="space-y-6">
              <h2 className="text-2xl font-bold text-gray-800 border-b-2 border-purple-200 pb-2 flex items-center gap-3">
                <GraduationCap className="w-7 h-7 text-purple-600" />
                අධ්‍යාපන සුදුසුකම්
              </h2>
              <div className="bg-linear-to-r from-gray-50 to-gray-100 p-6 rounded-xl border-l-4 border-purple-500">
                <label className="block mb-3">
                  <span className="text-lg font-semibold text-gray-800">
                    ඔබගේ අධ්‍යාපන සුදුසුකම් තෝරන්න
                  </span>
                </label>
                <GroupedMultiComboSelect
                  options={educationOptions}
                  placeholder="ඔබගේ අධ්‍යාපන සුදුසුකම් සොයන්න..."
                  onChange={handleEducationChange}
                  value={selectedEducation}
                />
              </div>
            </div>

            {/* Existing Survey Questions */}
            {Object.entries(groupedQuestions).map(
              ([category, categoryQuestions]) => (
                <div key={category} className="space-y-6">
                  <h2 className="text-2xl font-bold text-gray-800 border-b-2 border-purple-200 pb-2">
                    {categoryNames[category] || category}
                  </h2>

                  {(categoryQuestions as any[])
                    .filter(shouldShowQuestion)
                    .sort((a: any, b: any) => a.display_order - b.display_order)
                    .map((question: any) => (
                      <div
                        key={question.id}
                        className="bg-linear-to-r from-gray-50 to-gray-100 p-6 rounded-xl border-l-4 border-purple-500"
                      >
                        <label className="block mb-3">
                          <span className="text-lg font-semibold text-gray-800">
                            {question.question_text_si}
                          </span>
                        </label>
                        {renderQuestionInput(question)}
                      </div>
                    ))}
                </div>
              )
            )}

            {/* Deceased Family Members Section */}
            {answers.has_deceased_family === "yes" && (
              <div className="space-y-6">
                <div className="flex items-center justify-between">
                  <h2 className="text-2xl font-bold text-gray-800">
                    මිය ගිය පවුල් සාමාජිකයින්
                  </h2>
                  <button
                    onClick={addDeceasedMember}
                    className="flex items-center gap-2 bg-purple-600 text-white px-4 py-2 rounded-lg hover:bg-purple-700 transition-colors"
                  >
                    <Plus size={20} />
                    එකතු කරන්න
                  </button>
                </div>

                {deceasedMembers.map((member: any, index: any) => (
                  <div
                    key={member.id}
                    className="bg-linear-to-r from-red-50 to-orange-50 p-6 rounded-xl border-l-4 border-red-500"
                  >
                    <div className="flex justify-between items-center mb-4">
                      <h3 className="text-lg font-bold">
                        සාමාජිකයා #{index + 1}
                      </h3>
                      <button
                        onClick={() => removeDeceasedMember(member.id)}
                        className="text-red-600 hover:text-red-800 transition-colors"
                      >
                        <Trash2 size={20} />
                      </button>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-medium mb-2">
                          ඥාතිත්වය
                        </label>
                        <input
                          type="text"
                          value={member.relationship}
                          onChange={(e) =>
                            updateDeceasedMember(
                              member.id,
                              "relationship",
                              e.target.value
                            )
                          }
                          className="w-full p-3 border-2 border-gray-200 rounded-lg"
                          placeholder="උදා: පියා, මව, සහෝදරයා"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium mb-2">
                          උපන් දිනය
                        </label>
                        <input
                          type="date"
                          value={member.date_of_birth}
                          onChange={(e) =>
                            updateDeceasedMember(
                              member.id,
                              "date_of_birth",
                              e.target.value
                            )
                          }
                          className="w-full p-3 border-2 border-gray-200 rounded-lg"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium mb-2">
                          උපන් වේලාව
                        </label>
                        <input
                          type="time"
                          value={member.birth_time}
                          onChange={(e) =>
                            updateDeceasedMember(
                              member.id,
                              "birth_time",
                              e.target.value
                            )
                          }
                          className="w-full p-3 border-2 border-gray-200 rounded-lg"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium mb-2">
                          උපන් ස්ථානය
                        </label>
                        <input
                          type="text"
                          value={member.birth_location}
                          onChange={(e) =>
                            updateDeceasedMember(
                              member.id,
                              "birth_location",
                              e.target.value
                            )
                          }
                          className="w-full p-3 border-2 border-gray-200 rounded-lg"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium mb-2">
                          මිය ගිය වර්ෂය
                        </label>
                        <input
                          type="number"
                          value={member.year_of_death}
                          onChange={(e) =>
                            updateDeceasedMember(
                              member.id,
                              "year_of_death",
                              e.target.value
                            )
                          }
                          className="w-full p-3 border-2 border-gray-200 rounded-lg"
                          placeholder="වර්ෂය"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium mb-2">
                          මරණයට හේතුව
                        </label>
                        <input
                          type="text"
                          value={member.cause_of_death}
                          onChange={(e) =>
                            updateDeceasedMember(
                              member.id,
                              "cause_of_death",
                              e.target.value
                            )
                          }
                          className="w-full p-3 border-2 border-gray-200 rounded-lg"
                        />
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          <div className="bg-gray-50 p-8 border-t">
            <button
              onClick={handleSave}
              disabled={saving}
              className="w-full bg-linear-to-r from-purple-600 to-indigo-600 text-white py-4 rounded-xl font-bold text-lg hover:from-purple-700 hover:to-indigo-700 transition-all disabled:opacity-50 flex items-center justify-center gap-3"
            >
              {saving ? (
                <>
                  <Loader2 className="animate-spin" size={24} />
                  සුරකිමින්...
                </>
              ) : saveSuccess ? (
                <>
                  <CheckCircle size={24} />
                  සුරකින ලදී!
                </>
              ) : (
                <>
                  <Save size={24} />
                  තොරතුරු සුරකින්න
                </>
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}