"use client";

import { User, Loader2 } from "lucide-react";
import { useAuth } from "../context/AuthContext";
import Link from "next/link";
import { useEffect, useState, useRef } from "react";
import { useRouter } from "next/navigation";
import HoroscopeChart from "../components/horoscope/HoroscopeChart";
import DashaInformationCard from "../components/horoscope/DashaInfromationCard";
import PredictionSections from "../components/horoscope/predictionSections";
import AstroDataComponent from "../components/horoscope/astroDataCompunent";
import UserFeedbackSurvey from "../components/horoscope/userFeedbackServays";

const Horoscope = () => {
  const {
    user,
    isAuthenticated,
    GetPlanet,
    Getmahadasha,
    planet,
    mahadasha,
    anthardahsa,
    getAntharDasha,
    predictions,
    preditions,
    Getnavam,
    astro,
    AstroLogic,
    navam,
    loading: planetLoading,
    HandleFeedback,
    GetFeedbackStatus
  } = useAuth();

  const router = useRouter();

  const [allPredictionsLoaded, setAllPredictionsLoaded] = useState(false);
  const [isInitialLoad, setIsInitialLoad] = useState(true);
  const isProfileComplete = user?.isProfileComplete;

  const [feedbackStatus, setFeedbackStatus] = useState<{
    [key: string]: "correct" | "incorrect" | "Undecided" | null;
  }>({});

  const hasLoadedRef = useRef(false);
  const currentUserIdRef = useRef<string | null>(null);

  useEffect(() => {
    if (isAuthenticated == false) {
      router.push("/auth");
    }
  }, [isAuthenticated]);

  useEffect(() => {
    if (user?.id && user.id !== currentUserIdRef.current) {
      console.log("User changed, resetting data...");
      currentUserIdRef.current = user.id;
      hasLoadedRef.current = false;
      setAllPredictionsLoaded(false);
      setIsInitialLoad(true);
    }
  }, [user?.id]);

  useEffect(() => {
    if (!isAuthenticated) {
      hasLoadedRef.current = false;
      setAllPredictionsLoaded(false);
      setIsInitialLoad(true);
      currentUserIdRef.current = null;
      return;
    }

    if (!isProfileComplete || hasLoadedRef.current) return;

    hasLoadedRef.current = true;
    setIsInitialLoad(true);

        const loadFeedbackStatus = async () => {
      if (!isAuthenticated || !isProfileComplete) return;
      
      const feedbackData:any = await GetFeedbackStatus();
        const statusMap: { [key: string]: "correct" | "incorrect" | "Undecided" } = {};
        feedbackData.feedbacks.forEach((item: any) => {
          statusMap[item.key] = item.status;
        });
        setFeedbackStatus(statusMap);

        
    };

    const loadAllData = async () => {
      const planets = [
        "Sun",
        "Moon",
        "Mercury",
        "Venus",
        "Mars",
        "Saturn",
        "Jupiter",
        "Rahu",
        "Ketu",
      ];

      try {
        await Promise.all([
          GetPlanet(),
          Getmahadasha(),
          getAntharDasha(),
          Getnavam(),
          AstroLogic(),
        ]);
        await Promise.all(planets.map((planet) => preditions(planet)));

        setAllPredictionsLoaded(true);
        setIsInitialLoad(false);
      } catch (error) {
        console.error("Error loading data:", error);
        hasLoadedRef.current = false;
        setIsInitialLoad(false);
      }
    };

    loadAllData();
    loadFeedbackStatus();
  }, [isAuthenticated, isProfileComplete, user?.id]);

  useEffect(() => {
    if (!isInitialLoad) return;

    const requiredPlanets = [
      "Sun",
      "Moon",
      "Mercury",
      "Venus",
      "Mars",
      "Saturn",
      "Jupiter",
      "Rahu",
      "Ketu",
    ];

    const allLoaded = requiredPlanets.every(
      (planet) => predictions[planet]?.result?.data?.predictions
    );

    if (allLoaded) {
      setAllPredictionsLoaded(true);
      setIsInitialLoad(false);
    }
  }, [predictions, isInitialLoad]);

  if (!isAuthenticated || user === null) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-12 h-12 animate-spin text-blue-600 mx-auto mb-4" />
          <p className="text-gray-600 text-lg">
            Redirecting to authentication...
          </p>
        </div>
      </div>
    );
  }

  if (!isProfileComplete) {
    return (
      <div className="min-h-screen bg-linear-to-br from-blue-600 via-indigo-700 to-purple-800 flex items-center justify-center p-4">
        <div className="w-full max-w-md bg-white/10 backdrop-blur-lg rounded-3xl shadow-2xl p-10 border border-white/20">
          <div className="flex justify-center mb-8">
            <div className="bg-white/90 rounded-full p-6 shadow-2xl">
              <User className="w-20 h-20 text-indigo-700" strokeWidth={2.5} />
            </div>
          </div>
          <h1 className="text-white text-4xl font-bold text-center mb-4">
            පළමුව ඔබේ තොරතුරු පුරවන්න
          </h1>
          <p className="text-blue-100 text-center text-lg mb-10">
            නිවැරදි ජන්ම පත්‍රයක් ලබාගැනීමට ඔබේ උපන් දිනය, වේලාව සහ ස්ථානය
            අවශ්‍යයි
          </p>
          <Link href="/profile">
            <button className="w-full bg-white text-indigo-700 font-bold text-xl py-5 px-8 rounded-2xl hover:bg-blue-50 transition-all duration-300 shadow-2xl hover:shadow-indigo-500/50 transform hover:scale-105">
              ප්‍රොෆයිල් පිරවීමට යන්න
            </button>
          </Link>
        </div>
      </div>
    );
  }

  if (planetLoading || !allPredictionsLoaded || isInitialLoad) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-16 h-16 animate-spin text-purple-600 mx-auto mb-6" />
          <p className="text-xl text-gray-700 font-medium">
            {!allPredictionsLoaded
              ? "අනාවැකි ලබාගනිමින්..."
              : "ඔබේ ජන්ම පත්‍රය ගණනය කරමින්..."}
          </p>
          <p className="text-gray-500 mt-2">මද වේලාවක් රැඳී සිටින්න</p>
        </div>
      </div>
    );
  }

  if (
    !planet?.result?.data ||
    !mahadasha?.result?.data ||
    !anthardahsa?.result?.data ||
    !navam?.result?.data ||
    !astro?.result?.data
  ) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
        <div className="bg-red-50 border-2 border-red-200 rounded-2xl p-8 text-center max-w-md">
          <p className="text-red-700 text-xl font-semibold mb-2">
            ජන්ම පත්‍රය ලැබී නැහැ :(
          </p>
          <p className="text-red-600">නැවත උත්සාහ කරන්න හෝ පසුව එන්න</p>
        </div>
      </div>
    );
  }

  const data = planet.result.data;
  const mahadashaData = mahadasha.result.data;
  const AnthardashaData = anthardahsa.result.data;
  const Navam = navam.result.data;
  const astroData = astro.result.data;

  const handleCorrect = async (planet: string, text: string, cat: string) => {
    const result = await HandleFeedback(planet, text, cat, "correct");
    const key = `${result.key}`;
    setFeedbackStatus((prev) => ({
      ...prev,
      [key]: result.status,
    }));
  };

  const handleInCorrect = async (planet: string, text: string, cat: string) => {
    const result = await HandleFeedback(planet, text, cat, "incorrect");
    const key = `${result.key}`;
    setFeedbackStatus((prev) => ({
      ...prev,
      [key]: result.status,
    }));
  };

  const hanndleUndecided = async (
    planet: string,
    text: string,
    cat: string
  ) => {
    const result = await HandleFeedback(planet, text, cat, "Undecided");
    const key = `${result.key}`;
    setFeedbackStatus((prev) => ({
      ...prev,
      [key]: result.status,
    }));
  };

  const getPredictionContent = (planet: string) => {
    const predictionData = predictions[planet]?.result?.data?.predictions;

    if (!predictionData || !predictionData[0]) {
      return [];
    }

    const predictionContent = Object.entries(predictionData[0])
      .filter(([key]) => key !== "Predictionsname")
      .map(([key, value]: [string, any]) => ({
        title: key,
        predictionText: value
          .split(".")
          .filter((sentence: any) => sentence.trim() !== "")
          .map((sentence: any) => sentence.trim() + "."),
      }));

    return predictionContent;
  };

  const mahadashaItems = mahadashaData.current_mahadasha_set;
  const antardashaItems = AnthardashaData.current_antardasha_set;

  return (
    <div className="w-full min-h-screen px-4 py-6 md:py-8">
      <div className="flex flex-col lg:flex-row gap-4 w-full justify-center lg:items-center items-start pb-8 lg:gap-10 font-bold text-md shadow mb-4">
        <span className="flex gap-2 items-center">
          <div className="w-3 h-3 bg-blue-400 rounded-full"></div> Date of Birth
          - {user.dateOfBirth}
        </span>
        <span className="flex gap-2 items-center">
          <div className="w-3 h-3 bg-blue-400 rounded-full"></div>Time of Birth
          - {user.birthTime}
        </span>
        <span className="flex gap-2 items-center">
          <div className="w-3 h-3 bg-blue-400 rounded-full"></div>Birth location
          - {user.birthLocation}
        </span>
      </div>
      <div className="mx-auto">
        <div className="flex flex-col lg:flex-row gap-6 lg:gap-4 items-start justify-center">
          <div className="wrapper flex-col gap-14 w-full lg:w-1/2 flex justify-center items-center">
            <HoroscopeChart
              data={data}
              lagnaya={planet?.result?.data?.lagnaya}
              isHoroscope={true}
            />

            <HoroscopeChart
              data={Navam}
              lagnaya={navam?.result?.data?.lagnaya}
              isHoroscope={false}
            />
          </div>

          <div className="w-full lg:w-1/2 flex justify-center">
            <div className="flex flex-col gap-6 w-full">
              <DashaInformationCard items={mahadashaItems} ismahaDaha={true} />
              <DashaInformationCard
                items={antardashaItems}
                ismahaDaha={false}
              />
            </div>
          </div>
        </div>
      </div>

      <AstroDataComponent astroData={astroData} />

      <div className="group w-full mt-5">
        <div className="bg-white rounded-2xl shadow-xl hover:shadow-2xl transition-all duration-300 overflow-hidden border border-purple-100">
          <div className="bg-linear-to-r from-purple-600 to-indigo-600 p-4 sm:p-6">
            <h2 className="text-xl sm:text-2xl font-bold text-white tracking-wide">
              Prediction
            </h2>
            <div className="h-1 w-16 sm:w-20 bg-white/30 rounded-full mt-2"></div>
          </div>
          <div className="p-4 sm:p-6">
            <PredictionSections
              predictionTitle="සූර්‍යයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Sun")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Sun"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="චන්ද්‍රයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Moon")}
              planet="Moon"
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="බුධ ග්‍රහයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Mercury")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Mercury"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="ශුක්‍ර ග්‍රහයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Venus")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Venus"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="අඟහරු ග්‍රහයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Mars")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Mars"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="සෙනසුරු ග්‍රහයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Saturn")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Saturn"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="බ්‍රහස්පති ග්‍රහයාට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Jupiter")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Jupiter"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="රාහුට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Rahu")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Rahu"
              feedbackStatus={feedbackStatus}
            />
            <PredictionSections
              predictionTitle="කේතුට අනුව ඔබගේ බලපෑම"
              predictionContent={getPredictionContent("Ketu")}
              handleCorrect={handleCorrect}
              handleInCorrect={handleInCorrect}
              handleUndecided={hanndleUndecided}
              planet="Ketu"
              feedbackStatus={feedbackStatus}
            />
          </div>
        </div>
      </div>

      <UserFeedbackSurvey/>
    </div>
  );
};

export default Horoscope;
